// import { memo } from "react"

import { FC, memo, useCallback, useState } from "react";
import { Request } from "~/http/request";
import { EmbedProps } from "./types";
import Preview from "../../components/Previewer";
import { Space } from "antd";
import style from "./style.module.scss";

const Embed: FC<EmbedProps> = (props) => {
  const [codeValue, _] = useState(
    new Map<string, string>([
      ["script", props.data?.script],
      ["scriptType", props.data?.scriptType],
      ["css", props.data?.css],
      ["cssType", props.data?.cssType],
      ["html", props.data?.html],
      ["htmlType", props.data?.htmlType],
    ])
  );

  const handleHome = useCallback(() => {
    const url = "https://vwood.xyz/tiny-code/" + props.data.id;
    window.open(url)
  }, [])

  const handleRerun = useCallback(() => {
    location.reload();
  }, [])

  return (
    <div className={style["embed"]}>
      {/* <div className={style["embed-header"]}>
        <span className={style["embed-header-logo"]} onClick={handleHome}>在线编辑</span>
      </div> */}
      <div  className={style["embed-iframe"]}>
        <Preview codeMap={codeValue} />
      </div>
      <div className={style["embed-footer"]}>
        <Space>
        <button onClick={handleRerun}>刷新</button>
        <button onClick={handleHome}>在线编辑</button>
        </Space>
      </div>
    </div>
  );
};

export async function getStaticProps(context) {
  const id = context.params.id;
  // 获取当前id的代码信息
  const res = await Request.getChecklessTinyCode(id);

  return {
    props: { data: res.data, id },
  };
}

export async function getStaticPaths() {
  const res = await Request.getTinyCodeList({
    pageNo: 0,
    pageSize: 9999,
  });

  const tinyCodeList = res?.data?.data || [];
  const paths = tinyCodeList.map((tinyCode) => ({
    params: { id: tinyCode.id },
  }));

  return {
    paths,
    fallback: true, // false or 'blocking'
  };
}

export default memo(Embed);
